textarea {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-left: 10%;
  border-radius: 10px;
  outline: blueviolet;
  font-size: 15px;
  color: black;
  resize: none;
  border-width: 1px;
  background-color: rgb(231, 190, 128);
}

textarea:focus {
  background-color: rgb(231, 216, 129);
  resize: none;
  border-width: 2px;
  border-color: blueviolet;
}

.button_add_edit {
  margin-bottom: 15px;
  margin-left: 20px;
  font-size: 20px;
  margin-right: 20px;
}

.div_save_edit {
  border-radius: 10px;
  /* display: inline-block; */
  height: 30px;
  width: 12%;
  float:left;
  margin-left: 10%;
  margin-bottom: 4px;
  /* float:right; */
  color: blanchedalmond;
  background-color: rgb(21, 138, 76);
}

.div_save_edit:hover {
  background-color: rgb(30, 190, 105);
}
.div_save_edit:focus {
  background-color: rgb(11, 92, 49);
}

.button_delete {
  margin-bottom: 15px;
  margin-left: 20px;
  font-size: 20px;
  margin-right: 20px;
}

.div_delete {
  border-radius: 10px;
  /* display: inline-block; */
  height: 30px;
  float: right;
  width: 13%;
  margin-right: 22%;
  margin-bottom: 5%;
  /* float:right; */
  color: blanchedalmond;
  background-color: rgb(240, 14, 14);
}

.div_delete:hover {
  background-color: rgb(128, 5, 5);
}

.section_edit {
    font:900;
    margin-top: 60px;
    display: block;
}